* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: Roboto, serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.menu {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.nav {
  height: 100%;
  display: flex;
}

.link {
  text-transform: uppercase;
  color: #000;
  vertical-align: middle;
  height: 60px;
  margin-left: 20px;
  line-height: 60px;
  text-decoration: none;
  position: relative;
}

.link:first-of-type {
  margin-left: 0;
}

.link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  transition: all .2s;
  position: absolute;
  bottom: 0;
  left: 0;
}
